div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  .switch-checkbox {
    height: 0;
    width: 0;
    visibility: hidden;
  }
  
  .switch-label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    width: 150px;
    height: 50px;
    border-radius: 100px;
    position: relative;
    transition: background-color 0.2s;
  }
  
  .switch-label .switch-button {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    width: 45px;
    height: 45px;
    border-radius: 45px;
    transition: 0.2s;
    background: #fff;
    box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
  }
  
  .switch-checkbox:checked + .switch-label .switch-button {
    left: calc(100% - 2px);
    transform: translateX(-100%);
  }
  
  .switch-label:active .switch-button {
    width: 60px;
  }
  
  .greenButton {
    padding: 0.5rem 1rem;
    border-radius: 50px;
    background-color: #0c9d46;
    color: black;
  }
  
  .redButton {
    background-color: #ff3037;
    color: black;
  }
  